import React, { useEffect, useState } from 'react';
import './Accordion.scss';

interface Props {
	title?: string;
	children?: JSX.Element[] | JSX.Element | string;
	queryTarget?: boolean;
}

export const Accordion = ({ title, children, queryTarget = false }: Props) => {
	// Language
	// const { t } = useTranslation();
	// const lang = t('key');

	const [open, setOpen] = useState<boolean>(false);
	const [isTargetFromQuery, setIsTargetFromQuery] = useState<boolean>(queryTarget);

	useEffect(() => {
		setOpen(isTargetFromQuery);
	}, [isTargetFromQuery]);

	const toggleAccordion = () => {
		setOpen(!open);
	};
	const closeAccordion = () => {
		setOpen(false);
	};

	return (
		<div
			className={`accordion ${open ? 'mod-open' : ''}`}
		>
			<button
				type="button"
				className="accordion-toggle"
				onClick={() => { toggleAccordion(); }}
			>
				<span className="accordion-toggle-title">{title}</span>
				<span className="accordion-toggle-graphic" />
			</button>
			<div
				className="accordion-content"
			>
				{children}
			</div>
		</div>
	);
};

export default Accordion;
