import React, { useEffect, useState } from 'react';
import { useScrollPercentage } from 'react-scroll-percentage';
import { Link } from 'react-router-dom';
// eslint-disable-next-line @typescript-eslint/ban-ts-ignore
// @ts-ignore
import ParallaxMousemove from 'react-parallax-mousemove';
import { Accordion, PerspectiveBlock, ViewPortAnimation, ShapeOutText } from '../../components';
import './Artisan.scss';


interface Props {
	pageProgess: any;
	updatePageProgess: (status: any) => void;
	onClickSubsBtn: () => void;
}

const ArtisanGraphic = () => (
	<div className="sec-artisan__graphic">
		<div className="sec-artisan__wave-tag">
			<div className="sec-artisan__wave-text">
				LIMITED
				<br />
				GIFT
			</div>
		</div>
		<div className="sec-artisan__parallax">
			<ParallaxMousemove>
				<ParallaxMousemove.Layer
					config={{
						xFactor: 0.01,
						yFactor: -0.01,
					}}
				>
					<img className="sec-artisan__graphic-mask" src="/img/artisan/img-mask.png" alt="" />
				</ParallaxMousemove.Layer>
				<ParallaxMousemove.Layer
					config={{
						xFactor: 0.03,
						yFactor: -0.03,
					}}
				>
					<img className="sec-artisan__graphic-blur" src="/img/artisan/img-mask-blur.png" alt="" />
				</ParallaxMousemove.Layer>
			</ParallaxMousemove>
		</div>
	</div>
);


interface SubsProps {
	onClickSubsBtn: () => void;
}
const ArtisanSubs = ({ onClickSubsBtn }: SubsProps) => (
	<button
		className="sec-artisan__subs"
		type="button"
		onClick={() => onClickSubsBtn()}
	>
		<span className="sec-artisan__subs-txt">
			SUBSCRIBE
			<br />
			NOW
		</span>
	</button>
);

const Artisan = ({ pageProgess, updatePageProgess, onClickSubsBtn }: Props) => {
	const [ref, percentage] = useScrollPercentage({
		/* Optional options */
		threshold: 0,
	});

	const processPercentage = parseFloat(percentage.toPrecision(2));

	const sectionActive = processPercentage > 0.12;

	useEffect(() => {
		if (sectionActive && pageProgess.artisan !== 'active') {
			if (pageProgess.artisan !== 'active') {
				updatePageProgess({
					...pageProgess,
					artisan: 'active',
				});
			}
		} else if (pageProgess.artisan === 'active') {
			updatePageProgess({
				...pageProgess,
				artisan: '',
			});
		}
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [sectionActive]);

	return (
		<div
			ref={ref}
			className={`sec-artisan ${sectionActive ? 'is-active' : ''}`}
		>
			<div className="sec-artisan__bg" />
			<div className="sec-artisan__inner">
				<h2 className="sec-artisan__title">
					<span
						className="sec-artisan__title-icon"
						style={{
							backgroundImage: 'url(/img/artisan/icon-artisan.svg)',
						}}
					/>
					<small className="sec-artisan__title-tag">SUBSCRIPTION PROGRAM</small>
					<span className="sec-artisan__title-txt">
						<ShapeOutText
							text={['BECOME', 'AN ARTISAN']}
						/>
					</span>
				</h2>
				<div className="sec-artisan__content">
					<div className="sec-artisan__graphics">
						<ArtisanGraphic />
					</div>
					<div className="sec-artisan__desc">
						<p>
							Enjoyed the New World Development 50th Anniversary Celebration?
							Stay in touch and be the first to find out about our next artisanal events.
						</p>
						<p>
							Subscribe to the We are Artisans mailing list
							for free to stay up to date with the latest news of New World Group,
							and be in the inner circle of our year-round cultural activities.
						</p>
						<div className="sec-artisan__graphics sec-artisan__graphics--mobile">
							<ArtisanGraphic />
						</div>
						<div className="sec-artisan__desktop-item">
							<ArtisanSubs onClickSubsBtn={() => onClickSubsBtn()} />
						</div>
						<p>
							As a welcome gift, new subscribers will also receive a pack
							(5 individual masks each) of our 50th Anniversary Limited Edition
							NWD masks – all made in Hong Kong as a celebration of our heritage.
						</p>

						<Accordion title="MASK SPECIFICATIONS">
							<div className="sec-artisan__info">
								<div className="sec-artisan__info-item">Adult size: 17.5 x 9.5cm</div>
								<div className="sec-artisan__info-item">ASTM Level 2 (BFE&ge;98%; PFE&ge;98%)</div>
								<div className="sec-artisan__info-item">Manufactured in a certified ISO Class 8 cleanroom</div>
								<div className="sec-artisan__info-item">Fluid resistant to help reduce the wearer&apos;s exposure to blood and other bodily fluids</div>
								<div className="sec-artisan__info-item">Comfortable nose band and earloops that fit the wearer&apos;s facial contours for high breathability</div>
								<div className="sec-artisan__info-item">Hypoallergenic and latex-free</div>
							</div>
						</Accordion>
						<div className="sec-artisan__mobile-item">
							<ArtisanSubs onClickSubsBtn={() => onClickSubsBtn()} />
						</div>
					</div>
				</div>
			</div>
		</div>
	);
};

export default Artisan;
